                    isRequired: true,
                    type: 'checkbox',
                    tip: '',
                    answer: [],
                    listData: [
                      { label: '', value: '1' },
                      { label: '', value: '2' },
                      { label: '', value: '3' }
                    ]
                  }
                ]
              }
            }
          ]
        }
      ]
    },
    childList (e) {
      this.$forceUpdate()
      this.childAll = []
      this.$set(this.childAll, 1, this.childMsg1())
    },
    userAddAnswerAction (e) {
      if (Array.isArray(e.answer)) {
        userAddAnswer({
          value: JSON.stringify(e.answer), // 
          qpid: e.id, // id
          fornum: e.fornum, // for
          quid: localStorage.getItem('quid') // id
        }).then((data) => {
        }).catch((data) => {
        })
      } else {
        userAddAnswer({
          value: e.answer, // 
          qpid: e.id, // id
          fornum: e.fornum, // for
          quid: localStorage.getItem('quid') // id
        }).then((data) => {
        }).catch((data) => {
        })
      }
    },
    NextPage () {
      this.getChuShi() // 
      this.$router.replace('/QueDing')
    },
    returnUserList () {
      this.$router.replace('/UserSystem')
    }
  }
}
</script>
<style scoped>
.ban{width:220px !important;}
.ban{width:100% !important;}
.returnUserList{top:110px;right: 20px;}
.el-form-item{margin-bottom:8px !important;}
.el-form--label-top .el-form-item__label{padding-bottom:0 !important;}
.el-date-editor.el-input, .el-date-editor.el-input__inner{width:100% !important;}
.el-cascader{width:100% !important;}
.el-select{width:100% !important;}
</style>



